#home {
  padding: 30px 0;
}
#home .container {
  background-color: #fff;
  min-height: 84vh;
  display: flex;
}
#home .container .slider {
  width: 24%;
  min-width: 300px;
  min-height: 84vh;
  background-color: #F4F6F8;
  padding: 0 25px;
  box-sizing: border-box;
}
#home .container .slider .user {
  padding: 30px 0;
  text-align: center;
  border-bottom: 1px solid #e0e0e0;
}
#home .container .slider .user img {
  width: 88px;
}
#home .container .slider .user p {
  color: #333;
  font-size: 20px;
  padding-top: 10px;
}
#home .container .slider .userInfo {
  border-bottom: 1px solid #e0e0e0;
  padding: 17px 0;
}
#home .container .slider .userInfo .info {
  display: flex;
  color: #666;
  padding: 13px 0;
  font-size: 18px;
}
#home .container .slider .userInfo .info .flex {
  flex: 1;
  font-size: 18px;
  text-align: right;
}
#home .container .slider .userInfo .info .flex_r {
  padding-right: 5px;
}
#home .container .content {
  background-color: #fff;
  flex: 1;
  padding: 40px 2% 40px 30px;
}
#home .container .content .title {
  font-size: 24px;
  font-weight: bold;
  color: #333333;
  padding-bottom: 30px;
}
#home .container .content .listbox {
  padding-bottom: 6%;
}
#home .container .content .listbox .list {
  width: 290px;
  margin-right: 3%;
  display: inline-block;
  margin-bottom: 20px;
}
#home .container .content .listbox a,
#home .container .content .listbox .normal {
  width: 100%;
  background-color: #F9F9F9;
  display: flex;
  align-items: center;
  padding: 16px 12px;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
}
#home .container .content .listbox a img,
#home .container .content .listbox .normal img {
  width: 42px;
  margin-right: 12px;
}
#home .container .content .listbox a p:first-child,
#home .container .content .listbox .normal p:first-child {
  color: #333333;
  font-size: 16px;
}
#home .container .content .listbox a p,
#home .container .content .listbox .normal p {
  color: #999;
}
#home .container .content .listbox a .flex,
#home .container .content .listbox .normal .flex {
  flex: 1;
}
#home .container .content .listbox a .flex_r,
#home .container .content .listbox .normal .flex_r {
  color: #666666;
  font-weight: bold;
  font-size: 18px;
}
#home .container .content .listbox a .noAdmin,
#home .container .content .listbox .normal .noAdmin {
  position: absolute;
  right: .8%;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  display: none;
}
#home .container .content .listbox .active > img,
#home .container .content .listbox .active p {
  opacity: 0.4;
}
#home .container .content .listbox .active .noAdmin {
  display: block;
  opacity: 1;
}
